import React, { useEffect, useState } from "react";
import { gapi } from "gapi-script";
import { supabase } from "../components/supabaseClient";
import { useNavigate } from "react-router-dom";
import '../styles/login.css';
import Google from "../components/icon/google-icon";

export default function IndexLogin() {
    const clientID = "751310361292-44dpfaa1dfe6uggnt4hosdske6t3sneh.apps.googleusercontent.com";
    const navigate = useNavigate();

    useEffect(() => {
        const initializeGapi = async () => {
            try {
                await gapi.auth2.init({
                    client_id: clientID,
                });
                console.log("GAPI Initialized successfully");
            } catch (error) {
                console.error("Error initializing Google API:", error);
            }
        };

        gapi.load("client:auth2", initializeGapi);

        // Verificar si ya hay una sesión activa
        const checkSession = () => {
            const userId = localStorage.getItem('userId');
            if (userId) {
                navigate('/dashboard');
            }
        };

        checkSession();
    }, [clientID, navigate]);

    const handleLogin = async () => {
        const authInstance = gapi.auth2.getAuthInstance();
        try {
            const response = await authInstance.signIn();
            const profile = response.getBasicProfile();
            const userEmail = profile.getEmail();

            // Verificar si el usuario existe en Supabase
            const { data: existingUser, error: fetchError } = await supabase
                .from('usuarios')
                .select('*')
                .eq('email', userEmail)
                .single();

            if (fetchError && fetchError.code !== 'PGRST116') {
                throw fetchError;
            }

            if (existingUser) {
                // Usuario existente - guardar datos y redirigir
                localStorage.setItem('userId', existingUser.id);
                localStorage.setItem('userEmail', userEmail);
                localStorage.setItem('userName', profile.getName());
                navigate('/dashboard');
            } else {
                // Crear nuevo usuario
                const { data: newUser, error: insertError } = await supabase
                    .from('usuarios')
                    .insert([{
                        nombre: profile.getName(),
                        email: userEmail,
                        fecha_creacion: new Date().toISOString()
                    }])
                    .select()
                    .single();

                if (insertError) throw insertError;

                localStorage.setItem('userId', newUser.id);
                localStorage.setItem('userEmail', userEmail);
                localStorage.setItem('userName', profile.getName());
                navigate('/dashboard');
            }
        } catch (error) {
            console.error("Error en el proceso de login:", error.message);
            alert("Error al iniciar sesión. Por favor, intenta de nuevo.");
        }
    };

    return (
        <div className="login-container">
            <h1>TaskNix</h1>
            <button onClick={handleLogin} className="login-button">
                <Google width="1.5em" height="1.5em" /> Iniciar sesión con Google
            </button>
        </div>
    );
}