import React, { useState, useEffect } from 'react';
import { supabase } from "../components/supabaseClient";
import { useNavigate } from "react-router-dom";
import '../styles/dashboard.css';

const TodoList = () => {
  const [tasks, setTasks] = useState([]);
  const [newTask, setNewTask] = useState('');
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();
  
  // Obtener datos del usuario del localStorage
  const userId = localStorage.getItem('userId');
  const userName = localStorage.getItem('userName');

  useEffect(() => {
    // Verificar si el usuario está autenticado
    if (!userId) {
      navigate('/');
      return;
    }

    // Cargar tareas del usuario
    loadTasks();
  }, [userId, navigate]);

  const loadTasks = async () => {
    try {
      setLoading(true);
      const { data, error } = await supabase
        .from('notas')
        .select('*')
        .eq('usuario_id', userId)
        .order('fecha_creacion', { ascending: false });

      if (error) {
        throw error;
      }

      if (data) {
        setTasks(data.map(task => ({
          id: task.id,
          text: task.contenido,
          completed: task.completed || false
        })));
      }
    } catch (error) {
      console.error("Error al cargar las tareas:", error.message);
    } finally {
      setLoading(false);
    }
  };

  const addTask = async (e) => {
    e.preventDefault();
    if (newTask.trim()) {
      try {
        const { data, error } = await supabase
          .from('notas')
          .insert([{
            usuario_id: userId, // Asegúrate de que esto sea correcto
            contenido: newTask.trim(),
            fecha_creacion: new Date().toISOString(),
            completed: false // Asegúrate de que este campo exista en tu tabla
          }])
          .select()
          .single(); // Eliminar .single() si esperas múltiples registros

        if (error) throw error;

        if (data) {
          setTasks(prevTasks => [
            {
              id: data.id,
              text: data.contenido,
              completed: data.completed || false
            },
            ...prevTasks
          ]);
          setNewTask('');
        }
      } catch (error) {
        console.error("Error al añadir la tarea:", error.message);
      }
    }
  };

  const toggleTask = async (taskId) => {
    try {
      const task = tasks.find(t => t.id === taskId);
      const { error } = await supabase
        .from('notas')
        .update({ completed: !task.completed })
        .eq('id', taskId);

      if (error) throw error;

      setTasks(tasks.map(t =>
        t.id === taskId ? { ...t, completed: !t.completed } : t
      ));
    } catch (error) {
      console.error("Error al actualizar la tarea:", error.message);
    }
  };

  const deleteTask = async (taskId) => {
    try {
      const { error } = await supabase
        .from('notas')
        .delete()
        .eq('id', taskId);

      if (error) throw error;

      setTasks(tasks.filter(task => task.id !== taskId));
    } catch (error) {
      console.error("Error al eliminar la tarea:", error.message);
    }
  };

  const clearCompleted = async () => {
    try {
      const completedTaskIds = tasks
        .filter(task => task.completed)
        .map(task => task.id);

      if (completedTaskIds.length === 0) return;

      const { error } = await supabase
        .from('notas')
        .delete()
        .in('id', completedTaskIds);

      if (error) throw error;

      setTasks(tasks.filter(task => !task.completed));
    } catch (error) {
      console.error("Error al limpiar tareas completadas:", error.message);
    }
  };

  const handleLogout = () => {
    localStorage.clear();
    navigate('/');
  };

  if (loading) {
    return <div className="loading">Cargando tareas...</div>;
  }

  const pendingTasks = tasks.filter(task => !task.completed).length;

  return (
    <div className="todo-container">
      <div className="header-container">
        <h1>Mi Lista de Tareas</h1>
        <div className="user-info">
          <span>¡Hola, {userName || 'Usuario'}!</span>
          <button onClick={handleLogout} className="logout-button">
            Cerrar sesión
          </button>
        </div>
      </div>
      
      <form onSubmit={addTask} className="add-task-form">
        <input
          type="text"
          value={newTask}
          onChange={(e) => setNewTask(e.target.value)}
          placeholder="Añadir nueva tarea"
          className="task-input"
        />
        <button type="submit" className="add-button">+</button>
      </form>

      <div className="tasks-list">
        {tasks.length === 0 ? (
          <div className="no-tasks">No hay tareas pendientes</div>
        ) : (
          tasks.map(task => (
            <div key={task.id} className="task-item">
              <label className="task-label">
                <input
                  type="checkbox"
                  checked={task.completed}
                  onChange={() => toggleTask(task.id)}
                />
                <span className={task.completed ? 'completed' : ''}>
                  {task.text}
                </span>
              </label>
              <button
                onClick={() => deleteTask(task.id)}
                className="delete-button"
              >
                🗑️
              </button>
            </div>
          ))
        )}
      </div>

      <div className="todo-footer">
        <span>{pendingTasks} tareas pendientes</span>
        {tasks.some(task => task.completed) && (
          <button
            onClick={clearCompleted}
            className="clear-button"
          >
            Limpiar completadas
          </button>
        )}
      </div>
    </div>
  );
};

export default TodoList;
